/* -------------------Tag Styles------------------------ */
div {
  font-family: "Ubuntu", sans-serif;
}
h4 {
  font-family: "Ubuntu", sans-serif;
  font-weight: bold;
}

/* -------------------Other Styles------------------------ */

/* Main Heading style */
.main-heading {
  font-family: "Ubuntu", sans-serif;
  font-weight: bold;
  font-size: 30px;
}

.task-icon {
  color: #fdcb9e;
  font-size: 40px;
  overflow: hidden;
  transition-duration: 0.8s;
  transition-property: transform;
}

.task-icon:hover {
  color: tomato;
  transform: rotate(360deg);
  -webkit-transform: rotate(360deg);
}

.github-icon {
  color: #fdcb9e;
  font-size: 40px;
  overflow: hidden;
  transition-duration: 0.8s;
  transition-property: transform;
}

.github-icon:hover {
  color: tomato;
  transform: rotate(360deg);
  -webkit-transform: rotate(360deg);
}
/* Footer Style */
.footer {
  font-family: "Overpass Mono", monospace;
  height: 30px;
  font-size: 12px;
}

/* Style for the task list boards */
.board {
  margin-top: 20px;
}

/* Style for each task list */
.paper {
  padding: 20px 5% 20px 5%;
  height: 100%;
  width: 95%;
  margin-left: 2.5%;
}

/* Style for each card */
.card {
  margin: 10px 0 10px 0;
}

/* Style for card description */
.card-body {
  font-family: "Montserrat", sans-serif;
  font-size: 0.9rem;
}

.footer-link,
.footer-link:link,
.footer-link:visited,
.footer-link:focus,
.footer-link:active {
  color: black;
  text-decoration: none;
}

.footer-link:hover {
  font-weight: bolder;
  font-size: 0.8rem;
  color: #ec823a;
}
